var wxCharts = require("../../../utils/wxcharts.js");
var _self;
var Charts;

var Data = {
	series: [{
			data: 70,
			name: '优良'
		},
		{
			data: 8,
			name: '轻度污染'
		},
		{
			data: 5,
			name: '中度污染'
		},
		{
			data: 5,
			name: '重度污染'
		},
		{
			data: 2,
			name: '严重污染'
		}
	]
};
var width;
export default {
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: this.getNowFormatYear()+'市空气统计'
		});
		
		uni.getSystemInfo({
			success(res) {
				width = res.screenWidth - 10;
			}
		})
	},
	data() {
		return {
           sdate: this.getNowFormatYear(),
           array: ['2017','2018', '2019', '2020'],
           index: 2,
		}
	},
	onReady: function() {
		this.ShowCharts("charts", Data);
		//this.hideLoading();
	},
	methods: {
		bindPickerChange: function(e) {
		console.log('picker发送选择改变，携带值为', e.target.value)
		this.index = e.target.value
		
		uni.setNavigationBarTitle({
			title: this.array[this.index]+'市空气统计'
		});
		
		},
		
		/*显示图表*/
		ShowCharts: function(canvasId, data) {
			Charts = new wxCharts({
				canvasId: canvasId,
				type: 'pie',
				fontSize: 11,
				background: '#FFFFFF',
				animation: true,
				series: data.series,
				width: width,
				height: 280,
				dataLabel: true,
				pixelRatio:1,
			});
		},
		
		goDetail:function(id,storeName){			
			let detail = {
					id: id,
					storeName:storeName,
					date:this.array[this.index]
				}
				uni.navigateTo({
					url: "daytotal32?detail=" + encodeURIComponent(JSON.stringify(detail))
				})
		},
		
		getNowFormatYear: function() {
			var date = new Date();
			var seperator1 = "-";
			var year = date.getFullYear();
			var currentdate = year ;
			return currentdate;
		},
	}
}
